
import { Toast } from 'antd-mobile';
/**
 * @desc   
 * @date   2018-04-28
 * @author luozhou
 * @param  {[Number]}   px []
 * @return {[Number]}      []
 */
const initWidth = 750;
const currentWidth = window.innerWidth;
export const MathPx = (px)=>{
	return px/(initWidth/currentWidth)
}
/**
 * @desc   
 * @date   2018-04-28
 * @author luozhou
 * @param  {[String]}   url     []
 * @param  {[Object]}   params  []
 * @param  {[Function]} resolve []
 */
const post = (url,params,resolve)=>{
	let body = Object.keys(params).length ? params : {};
	fetch(url,{
	    type:'post',
	    method:'post',
	    body,
	    headers:{
	        'Accept': 'application/x-www-form-urlencoded',
	        'Content-Type': 'application/x-www-form-urlencoded',
	    }
	}).then((response)=>{
	    if( !response.ok ) console.log('');
	    return response.json();
	}).then((json)=>{
		if(json.code==200){
	    	resolve(json.data)
		}
	}).catch((ex)=>{
	    console.trace(ex);
	})	
}
/**
 * @desc   
 * @date   2018-04-28
 * @author luozhou
 * @param  {[String]}   url     []
 * @param  {[Function]} resolve []
 */
const get = (url,resolve)=>{
	fetch(url,{
	    type:'get',
	    method:'get'
	}).then((response)=>{
	    if( !response.ok ) console.log('');
	    return response.json();
	}).then((json)=>{
	    if(json.code==200){
	    	resolve(json.data)
		}
	}).catch((ex)=>{
	    console.trace(ex);
	})	
}
/**
 * @desc   
 * @date   2018-04-28
 * @author luozhou
 * @param  {[String]}   url     []
 * @param  {[Object]}   params  []
 * @param  {[Function]} resolve []
 */
const file = (url,params,resolve)=>{
	let body = Object.keys(params).length ? params : {};
	fetch(url,{
	    type:'file',
	    method:'post',
	    body
	}).then((response)=>{
	    if( !response.ok ) console.log('');
	    return response.json();
	}).then((json)=>{
	    if(json.code==200){
	    	resolve(json.data)
		}
	}).catch((ex)=>{
	    console.trace(ex);
	})	
}

export const Ajax = {
	post,
	get,
	file
}


